@import url('https://fonts.googleapis.com/css2?family=El+Messiri:wght@400;500;600;700&family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

body {
  margin: 0;
  font-family: 'Montserrat', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*, *::after, *::before {
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}
@keyframes tagAnimatedOne {
  from {
    transform: translateY(-20%) translateX(-10%);
  }
  to {
    transform: translateY(20%) translateX(10%);
  }
}
@keyframes tagAnimatedTwo {
  from {
    transform: translateY(20%) translateX(10%);
  }
  to {
    transform: translateY(-20%) translateX(-10%);
  }
}
@keyframes dotAnimated {
  from {
    transform: rotate(0deg) translate3d(10px, 0, 0) rotate(0deg);
  }

  to {
    transform: rotate(360deg) translate3d(10px, 0, 0) rotate(-360deg);
  }
}
@keyframes dotAnimatedReverse {
  from {
    transform: rotate(360deg) translate3d(10px, 0, 0) rotate(-360deg);
  }

  to {
    transform: rotate(0deg) translate3d(10px, 0, 0) rotate(0deg);
  }
}